import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import axios from 'axios'
import { withRouter } from 'react-router-dom'

import facebookLogo from '../../assets/imgs/facebook-icon.svg'
import googleLogo from '../../assets/imgs/google-icon.svg'
import PassWordInput from '../../elements/PasswordInput/PasswordInput'

import Loader from 'react-loader-spinner'
import { notify } from '../../reducers/popUp'
import { loginUser } from '../../reducers/user'


import { Form, FormTitle, SubmitButton, AuthSocial, Input, Divider, Agreement, TwoInOneLine} from './RegistrationFormStyled'
import { useInputChange } from '../../utils/useInputChange'

import countryList from './CountryList'
import phoneList from './phoneList'

import { register } from '../../services/user'
import globeIcon from './globe.png'




const RegistrationForm = (props) => {

    useEffect(() => {
        return () => {
            props.notify('')
        }
    }, [])

    const [ loading, setLoading ] = useState(false)
    const [ flag, setFlag ] = useState(globeIcon)

    const [ inputData, handleInputChange ] = useInputChange()
    const [ country, setCountry ] = useState('AF')
    const [ dialCode, setDialCode] = useState('')
    const [ phone, setPhone] = useState('')
    const [ city, setCity ] = useState('')

    useEffect(() => {
        axios.get(`https://api.ipdata.co?api-key=94ad779a265d3957a6af4449f3dcbf1543de2dc55843fb41f249b359`)
        .then((res) => {
            setCountry(res.data.country_code)
        })
        .catch((err) => {
            console.log(err)
        })
    }, [])

    useEffect(() => {

        setFlag(globeIcon)

        let finded = phoneList.find(item => item.dial_code === dialCode)
        console.log(finded)

        axios.get(`https://restcountries.eu/rest/v2/alpha/${finded.code}`)
        .then((res) => {
            setFlag(res.data.flag)

        })
        .catch((err) => {
            console.log(err)
        })
    }, [dialCode])

    useEffect(() => {
        if(country) {
            let finded = phoneList.find(item => item.code === country)
            setDialCode(finded.dial_code)
        }

    }, [country])

    const handleSubmit = (e) => {
        e.preventDefault()
        setLoading(true)

        let dataToSend = {}

        if(city) {
             dataToSend = {
                ...inputData,
                phone: dialCode + phone,
                city: city
            }
        } else {
            dataToSend = {
                ...inputData,
                phone: dialCode + phone,
            }
        }

        props.notify('')

        register(dataToSend)
        .then((res) => {
            setLoading(false)
            console.log(res)
            props.notify({
                heading: ' ',
                type: 'success',
                text: '  !'
            })
            props.loginUser(res.token)
            props.history.push('/main/profile')

        })
        .catch((err) => {
            console.log(err)
            setLoading(false)
            props.notify({
                heading: '',
                type: 'error',
                text: ' '
            })
        })
        console.log(dataToSend)
    }

    const handleDialCode = (e) => {
        setDialCode(e.target.value)
    }

    return (
        <Form onSubmit={handleSubmit}>
            <FormTitle>
                <h2> </h2>
                <p><small>  </small></p>
            </FormTitle>

            <Input id="username" onChange={handleInputChange} aria-label="" required type="text" autoComplete="false" placeholder="*"/>
            <Input id="email" onChange={handleInputChange} aria-label=" " required type="email" autoComplete="false" placeholder="Email *"/>
            <Input id="first_name" onChange={handleInputChange} aria-label="" type="text" required autoComplete="false" placeholder="*"/>
            <Input id="last_name" onChange={handleInputChange} aria-label="" type="text" required autoComplete="false" placeholder="*"/>

            <TwoInOneLine colWidth="50">
                <div>
                <Input id="date" onChange={handleInputChange} required aria-label="" type="date" placeholder=" *"/>
                <select id="gender" onChange={handleInputChange} aria-label="">
                    <option value="male"></option>
                    <option value="female"></option>
                </select>
                </div>

            </TwoInOneLine>

            <select id="country" value={country} onChange={(e) => setCountry(e.target.value)} aria-label="" required type="text" placeholder="*">
                {countryList.map((item) => (
                    <option key={item.code} value={item.code}>{item.name}</option>
                ))}
            </select>

            <Input aria-label="" value={city} onChange={(e) => setCity(e.target.value)} type="text" placeholder="*"/>
            
  
            <TwoInOneLine className="country" colWidth="30">
                <img className="flag" src={flag}/>
                <div>
                    <select required value={dialCode} onChange={handleDialCode} aria-label=" " type="tel" placeholder="+996">
                        {phoneList.map((item) => (
                            <option key={item.code} value={item.dial_code}>{item.code}: {item.dial_code}</option>
                        ))}
                    </select>
                    <Input value={phone} onChange={(e) => setPhone(e.target.value)} required aria-label="   " type="tel" placeholder=" *"/>

                </div>
 
            </TwoInOneLine>

            <PassWordInput bgColor="#E9E9E9">
                <Input required id="password" onChange={handleInputChange} aria-label="" autoComplete="new-password" placeholder="*"/>
            </PassWordInput>

            <PassWordInput bgColor="#E9E9E9">
                <Input required id="confirm_password" onChange={handleInputChange} aria-label=" " autoComplete="new-password" placeholder=" *"/>
            </PassWordInput>


            <SubmitButton disabled={loading ? true : false} red type="submit">{!loading ? '' : <Loader
                        type="TailSpin" color="#ffffff" height={15} width={15}
                    />}</SubmitButton>
            <Divider><div>  ? <b><Link to="/login"></Link></b></div></Divider>

            {/* <AuthSocial blue><img src={facebookLogo} alt="facebook logo"/><div>   Facebook</div></AuthSocial>
            <AuthSocial red><img src={googleLogo} alt="google logo"/><div>   Google</div></AuthSocial> */}
            
          
            <Agreement>,    <b><br/> </b>   <br/>  Survey App</Agreement>
        </Form>
    )
}

export default withRouter(connect(null, { notify, loginUser })(RegistrationForm))